
import Form from "./core/Form"
import Vue from "vue";
import axios from "axios";

window.axios = axios;
window.Form = Form;

new Vue({
	
	el: "#app",

	data: {
		form: new Form({
			name: "",
			description: "",
		}),
	},

	methods: {
		onSubmit() {
			this.form.post("axios");
		},

	},

	mounted() {

		axios.get("/hit").then(response => {
			console.log("in /hit:", response, response.data.one, response.data.two);
		});

		axios.get("/hit").then(response => {
			this.actions = new Array();
			this.actions.push(response.data.one);
			this.actions.push(response.data.two);
		});
	},

});
